/**
 * Basic typography style for copy text
 */
body {
  color: $text-color;
  font: normal 125% / 1.4 $text-font-stack;
}

.title-h1 {
  @include text-title-h1;
  margin: 0px 0px 32px 0px;
  @media (max-width: $md){
  display: none;
  }
}

.description {
  &__plan {
    @include text-description;
    margin: 0px 0px 46px 0px;
    @media (max-width: $md){
    display: none;
    }
  }
}

.title {
  margin: 94px 0 40px 0;
  text-align: center;
  &__h2 {
    @include text-title_h2;
  }
  @media (max-width: $md){
    margin: 20px 0 20px 0;
  }
}

.steps {
  &__descr {
    @include text-step-descr;
  }
}
