    box-shadow: 0px 4px 3px rgba(13, 96, 111, 0.25);
    border-radius: 10px;
    z-index: 6;
    transition: right 1s ease-out 0.5s;
  }
  .menu-active::after {
    content: "";
  }
}
.menu__overlay {
  display: none;
  position: fixed;
  top: 0;
  left: -100vw;
  width: 1000vw;
  height: 100vh;
}
.menu__list {
  display: flex;
}
@media (max-width: 768px) {
  .menu__list {
    display: none;
  }
}
