    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
.menu__item {
  z-index: 3;
}
.menu__item:nth-last-child(n+2) {
  margin: 0px 43px 0px 0px;
}
@media (max-width: 768px) {
  .menu__item:nth-last-child(n+2) {
    margin: 0px 15px 10px 0px;
  }
}
