    margin: 0px 15px 0px 0px;
  }
  .menu__item:nth-child(1) {
    margin: 44px 15px 10px 0px;
  }
}
.menu__link {
  font-family: "Red Hat Text", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 21px;
  color: #ffffff;
}
.menu__link:hover {
  cursor: pointer;
  opacity: 0.8;
  color: #ffffff;
}
.menu__icon {
  display: none;
}
@media (max-width: 768px) {
  .menu__icon {
    z-index: 3;
    display: block;
    width: 35px;
    height: 32px;
    cursor: pointer;
    position: relative;
  }
  .menu__icon::before {
    content: "";
    left: 0;
    position: absolute;
    height: 2.67px;
    width: 26.25px;
    transition: all 0.3s ease 0s;
    background-color: #fff;
    top: 5px;
  }
  .menu__icon::after {
    content: "";
    left: 0;
    position: absolute;
    height: 2.67px;
    width: 26.25px;
    transition: all 0.3s ease 0s;
    background-color: #fff;
    bottom: 12px;
  }
  .menu__icon span {
    left: 0;
    position: absolute;
    height: 2.67px;
    width: 26.25px;
    transform: all 0.3s ease 0s;
    background-color: #fff;
    top: 61px;
    transform: scale(1) translate(0px, -50px);
  }
}

.menu-active > .menu__close {
  position: absolute;
  width: 30px;
  height: 30px;
  left: 15.45px;
  top: 15.45px;
